import React from 'react';
import 'bootstrap/dist/css/bootstrap.css';

let customerDetails =
{
    firstName: "Sanjay",
    middleName: "Bajjuri",
    lastName: "Bajjuri",
    phoneNumber: 1545154,
    age: 25
};

function SetDetails(props) {
    alert("Method called");
    props.firstName = props.firstName + "123";
    alert(props.firstName);
}

function CreateAccount() {
    return (
        <>
            <div className="container">
                <div className="row">
                    <div className="col-sm-6">
                        <h1> First Name</h1>
                    </div>
                    <div className="col-sm-6">
                        <input type="text" value={customerDetails.firstName}></input>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-6">
                        <h1> Middle Name</h1>
                    </div>
                    <div className="col-sm-6">
                        <input type="text" value={customerDetails.middleName}></input>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-6">
                        <h1> Last Name</h1>
                    </div>
                    <div className="col-sm-6">
                        <input type="text" value={customerDetails.lastName}></input>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-6">
                        <h1> Phone Number</h1>
                    </div>
                    <div className="col-sm-6">
                        <input type="text" value={customerDetails.phoneNumber}></input>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-6">
                        <h1> Mail Id</h1>
                    </div>
                    <div className="col-sm-6">
                        <input type="text"></input>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-6">
                        <h1> Age</h1>
                    </div>
                    <div className="col-sm-6">
                        <input type="text" value={customerDetails.age}></input>
                    </div>
                </div>
                <br></br>
                <div className="btn btn-primary" onClick={() => SetDetails(customerDetails)}>Submit</div>
            </div>
        </>
    );
}

export default CreateAccount;