import React from 'react';
import 'bootstrap/dist/css/bootstrap.css';


const UpvotesSection = ({ name, upvotes, setLatestData }) => {
    const upvoteArticle = async () => {
        console.log('Method called here');
        const result = await fetch(`/api/playerlist/${name}/upvote`,
            {
                method: 'post',
            });

        const body = await result.json();
        setLatestData(body);
    };

    return (
        <>
            <h1>Upvotes section:</h1>
            <button className='btn btn-primary' onClick={() => upvoteArticle()}>Add UpVote</button>
            <p>This player has been selectd by {upvotes} times</p>
        </>
    );
};

export default UpvotesSection;