import React from 'react';
import { Link } from 'react-router-dom';

const PlayerContentDetails = ({ playerDetails }) => {
    return (
        <>
            {playerDetails.map((m, key) => (
                <Link key={key} to={`/Selection/${m.name}`}>
                    <h2>
                        {m.name}</h2></Link>))}

        </>
    );
}

export default PlayerContentDetails;