import React from 'react';
import logo from './logo.svg';
import './App.css';
import 'bootstrap/dist/css/bootstrap.css';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import HomePage from './Pages/HomePage';
import LoginPage from './CricketingApp/LoginPage';
import MatchesList from './CricketingApp/MatchesList';
import PlayerSelection from './CricketingApp/PlayerSelection';
import CreateAccount from './CricketingApp/CreateAccount';
import Navbar from './NavBar';
import NotFoundPage from './CricketingApp/NotFoundPage';



function App() {
  return (
    <React.Fragment>
      <Router>
        <div className="App">
          <Navbar />
          <Switch>
            <Route path="/Home" component={HomePage} exact />
            <Route path="/Login" component={LoginPage} />
            <Route path="/Account" component={CreateAccount} />
            <Route path="/Matches" component={MatchesList} />
            <Route path="/Selection/:name" component={PlayerSelection} />
            <Route component={NotFoundPage} />
          </Switch>
        </div>
      </Router>
    </React.Fragment>
  );
}

export default App;
