import React, { useEffect, useState } from 'react';
import playerDetails from './PlayerContent';
import PlayerContentDetails from '../Components/PlayerContentDetails';
import NotFoundPage from './NotFoundPage';
import UpvotesSection from '../Components/UpvotesSection';
import AddCommentForm from '../Components/AddCommentForm';

const PlayersSelectionList = ({ match }) => {
    const name = match.params.name;
    const playerDetail = playerDetails.find(m => m.name === name);

    const [playerDetailData, setPlayerDetailData] = useState({ upvotes: 0, comments: [] });
    const otherPlayerDetails = playerDetails.filter(m => m.name !== name);

    useEffect(() => {
        console.log('Method called');
        const fetchData = async () => {
            const result = await fetch(`/api/getallplayers/${name}`);
            const body = await result.json();
            console.log(body);
            setPlayerDetailData(body);
        }
        fetchData();
    }, [name]);

    if (!playerDetail) return <NotFoundPage />
    return (
        <>
            <h1> Player has {playerDetailData.upvotes}</h1>
            <UpvotesSection name={playerDetail.name} upvotes={playerDetailData.upvotes} setLatestData={setPlayerDetailData} />
            <div>Comments are: {playerDetailData.comments.map((comment, key) => (
                <div key={key}>
                    <h2>{comment.username}</h2>
                    <h4>{comment.text}</h4>
                </div>))}
            </div>
            <h1> Player Name : {playerDetail.name} </h1>
            <h1> Player Batting Style {playerDetail.battingstyle} </h1>
            <h1> Player Bowling Style :{playerDetail.bowlingstyle} </h1>
            <AddCommentForm name={playerDetail.name} setLatestData={setPlayerDetailData} />
            <br />
            <h1>other Players For Selection:</h1>
            <PlayerContentDetails playerDetails={otherPlayerDetails} />
        </>
    )

}

export default PlayersSelectionList;