import React from 'react';
import { Link } from 'react-router-dom';

const NavBar = () => (
    <nav>
        <ul className="ulClass">
            <li className="liClass">
                <Link to="/">Home</Link>
            </li>
            <li className="liClass">
                <Link to="/Login">Login</Link>
            </li>
            <li className="liClass">
                <Link to="/Account">Account</Link>
            </li>
            <li className="liClass">
                <Link to="/Matches">Matches</Link>
            </li>
        </ul>
    </nav>
);

export default NavBar;
