let city = {
  name: "Tennesse",
  country: "America"
};

const lakeList = [
  { id: "1", name: "Ganga", length: "2000kms" },
  { id: "2", name: "Yamuna", length: "1000kms" },
  { id: "3", name: "Yamuna", length: "1500kms" }
];

function DisplayLake(props) {
  return (
    <h1> {props.lakename} </h1>
  )
}

function ChangeState(props) {

  const [year, setYear] = useState(2020);
  const [status, setStatus] = useState("Open");
  const [checked, setChecked] = useState(false);
  const [checkBox, toggle] = useReducer(checkBox => !checkBox, false);
  const [firstname, setFirstname] = useState("");
  const [lastname, setLastname] = useState("");
  const [data, setData] = useState(null);

  useEffect(() => {
    console.log({ firstname });
  }, [firstname]);
  useEffect(() => {
    console.log({ lastname });
  }, [lastname])
  // useEffect(() => {
  //   fetch(`https://api.github.com/users/${props.login}`).then(res => res.json()).then(setData).catch(console.error);
  // })
  return (
    <React.Fragment>
      <div className="container-fluid">
        <div>
          <h1> State : {status}</h1>
          <button className="btn btn-primary" onClick={() => setStatus("Open")}>Open</button>
          <button className="btn btn-success" onClick={() => setStatus("Closed")}>Closed</button>
        </div>
        <div>
          <h1>Current Year is : {year}</h1>
          <button onClick={() => setYear(year => year + 1)}>Next year!</button>
        </div>
        <div>
          <input type="checkbox" checked={checked} onChange={() => setChecked(checked => !checked)} /> {checked ? "Cheked" : "Not Checked"}
        </div>
        <div>
          <label>First Phrase</label>
          <input value={firstname} onChange={e => setFirstname(e.target.value)}>
          </input>
          <br />
          <label>Second Phrase</label>
          <input value={lastname} onChange={e => setLastname(e.target.value)}>
          </input>
        </div>
        {/* <div>
        {JSON.stringify(data)}
      </div> */}
        <div>
          <input type="checkbox" className="bi bi-archive" checked={checkBox} onChange={toggle} /> {checkBox ? "Cheked" : "Not Checked"}

        </div>
      </div>

    </React.Fragment >
  );
}

function Hello(props) {
  if (props.display === "true") {
    return (
      <div>
        <React.Fragment>
          <DisplayLake lakename="Ganga"></DisplayLake>
          <DisplayLake lakename="Yamuna"></DisplayLake>
        </React.Fragment>
        {props.library}
        <ul>
          {props.lakes.map(lake =>
            <div key={lake.id}>
              <h1>{lake.name}</h1>
              <p>Length of river is {lake.length}</p>
            </div>)}
        </ul>

      </div>
    );
  }
  else if (props.display === "false") {
    return (
      <h1> nothing to displayu</h1>
    )
  }
}


